* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: black;
}

p {
  margin: 0;
  padding: 0;
}

button,
input {
  outline: none;
  border: 1px solid transparent;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  background: #1d1f1f;
}

.container {
  width: 1140px;
  max-width: 100%;
  margin: 0 auto;
}

.button {
  border-radius: 27px;
  background-color: #ff3c00;
  color: #fff;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 700;
  padding: 15px 30px;
  cursor: pointer;
  transform: 0.4s;
}

.title {
  font-size: 36px;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;

  &--black {
    color: black;
  }
}

.bigtitle {
  font-size: 48px;
  color: #202123;
  font-weight: 700;
  // text-transform: uppercase;

  &--white{
    color: #fff;
  }
}
