.contacts {
  text-align: right;

  &__phone {
    font-size: 26px;
    color: #ffffff;
    font-weight: 400;
    margin-bottom: 5px;
    display: block;
  }

  &__graph {
    display: inline-block;
    position: relative;

    span {
      font-size: 13px;
      color: #ff5400;
      font-weight: 400;
      text-decoration: underline;
      cursor: pointer;
    }

    &:hover {
      .schedule {
        right: 0;
        opacity: 1;
      }
    }

    .schedule {
      display: inline-block;
      width: 156px;
      background-color: #d2d3d3;
      padding: 20px 18px;
      border-radius: 6px;
      text-align: left;
      position: absolute;
      top: 100%;
      right: -9999px;
      margin-top: 12px;
      opacity: 0;
      transition: opacity .4s;

      &__text {
        font-size: 13px;
        color: #1e1f20;
        font-weight: 400;
        white-space: nowrap;

        &--bold {
          font-weight: 700;
        }
      }

      &:after,
      &:before {
        content: "";
        display: inline-block;
        position: absolute;
        bottom: 100%;
      }

      &:after {
        right: 10px;
        border: 12px solid transparent;
        border-bottom-color: #d2d3d3;
      }

      &:before {
        height: 18px;
        width: 100%;
        display: block;
        left: 0;
        background-color: transparent;
      }
    }
  }

  &__text {
    font-size: 11px;
    color: #999999;
    font-weight: 400;
    margin-bottom: 5px;
  }
}
