.menu {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 58px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    font-size: 14px;
    color: #ffffff;
    font-weight: 400;
    transition: .3s;

    &:hover {
        opacity: .7;
    }
  }
}
