.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 54px;

  &--black-color{
    margin-top: 68px;
    
    .pagination__display , .pagination__link{
      color: black;
    }

    .pagination__item{
      &--active {
        .pagination__link {
          color: #fff;
        }
      }
    }

    .pagination__link{
      &:hover{
        color: #fff;
      }
    }
  }

  &__display {
    color: #ffffff;
    font-weight: 400;
    margin-right: 15px;
  }

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }

    &--active {
      .pagination__link {
        background-color: red;
      }
    }
  }

  &__link {
    display: inline-block;
    color: #ffffff;
    font-weight: 700;
    border-radius: 50%;
    padding: 5px;
    background-color: transparent;
    padding: 4px 11px;
    transition: 0.3s;

    &:hover {
      background-color: red;
    }
  }
}
