.about {
  margin-top: 151px;

  .container{
    display: flex;
  }
  
  &__img {
    width: 485px;
    height: 500px;
    flex-shrink: 0;
    border-radius: 6px;
    overflow: hidden;

    &-pic {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__content {
    border-radius: 6px;
    background-color: #ffffff;
    padding: 75px 100px;
    margin-top: 106px;
    margin-left: -129px;
  }

  &__title {
    margin-bottom: 35px;
  }

  &__text {
    line-height: 27px;
    color: #333333;
    font-weight: 400;
    margin-bottom: 91px;
  }
}
