.breadcrumbs {
  margin-top: 77px;

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 4px;

    &:after {
      content: "/";
      display: inline-block;
      color: #999999;
    }

    &:last-child:after {
      display: none;
      margin-right: 0;
    }

    &--active {
      .breadcrumbs__link {
        text-decoration: underline;
        color: #7b7b7c;
      }
    }
  }

  &__link {
    font-size: 14px;
    color: #999999;
    font-weight: 400;
  }
}
