.socials {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: inline-block;
    height: 35px;
    width: 35px;
    border-radius: 50%;
    background-color: #4b4c4d;
    transition: 0.4s;
    position: relative;

    &:hover {
      background-color: #787979;
    }

    &:before {
      content: "";
      display: inline-block;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    &:before {
      background-image: url(../../img/icons/socials/telegram.png);
      height: 36px;
      width: 36px;
    }
  }
}
