.partners{
    background-color: #f4f5f5;
    padding-top: 85px;
    padding-bottom: 102px;

    &__title{
        margin-bottom: 73px
    }

    &__list{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: -66px;
    }

    &__item{
        width: calc(100% / 4);
        margin-bottom: 66px;
        display: inline-flex;
        justify-content: center;

        &:nth-child(4){
            margin-right: 0;
        }

        &:last-child{
            margin-right: 0;
        }
    }

    &__link{
        display: inline-block;
    }
}