}

.breadcrumbs__list {
  display: flex;
  align-items: center;
}

.breadcrumbs__item {
  margin-right: 4px;
}

.breadcrumbs__item:after {
  content: "/";
  display: inline-block;
  color: #999999;
}

.breadcrumbs__item:last-child:after {
  display: none;
  margin-right: 0;
}

.breadcrumbs__item--active .breadcrumbs__link {
  text-decoration: underline;
  color: #7b7b7c;
}

