.contacts {
  text-align: right;
}

.contacts__phone {
  font-size: 26px;
  color: #ffffff;
  font-weight: 400;
  margin-bottom: 5px;
  display: block;
}

.contacts__graph {
  display: inline-block;
  position: relative;
}

.contacts__graph span {
  font-size: 13px;
  color: #ff5400;
  font-weight: 400;
  text-decoration: underline;
  cursor: pointer;
}

.contacts__graph:hover .schedule {
  right: 0;
  opacity: 1;
}

.contacts__graph .schedule {
  display: inline-block;
  width: 156px;
  background-color: #d2d3d3;
  padding: 20px 18px;
  border-radius: 6px;
  text-align: left;
  position: absolute;
  top: 100%;
  right: -9999px;
  margin-top: 12px;
  opacity: 0;
  transition: opacity .4s;
}

.contacts__graph .schedule__text {
  font-size: 13px;
  color: #1e1f20;
  font-weight: 400;
  white-space: nowrap;
}

.contacts__graph .schedule__text--bold {
  font-weight: 700;
}

.contacts__graph .schedule:after,
.contacts__graph .schedule:before {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: 100%;
}

.contacts__graph .schedule:after {
  right: 10px;
  border: 12px solid transparent;
  border-bottom-color: #d2d3d3;
}

.contacts__graph .schedule:before {
  height: 18px;
  width: 100%;
  display: block;
  left: 0;
  background-color: transparent;
}

.contacts__text {
  font-size: 11px;
  color: #999999;
  font-weight: 400;
  margin-bottom: 5px;
}

