.bg-section {
  background: url(../../img/bg/bottom-pic.jpg) no-repeat;
  background-position: center 215px;
  background-size: auto;
  margin-top: 146px;
}

.welcome {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url(../../img/bg/bottom-pic.jpg) center no-repeat;
  background-size: cover;
  position: relative;
}

.welcome:before {
  content: "";
  display: block;
  background: url(../../img/bg/bottom-pic-ot.png) center no-repeat;
  background-size: cover;
  width: 1200px;
  height: 736px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.2;
}

.welcome__content {
  text-align: center;
  animation: pulse 0.8s linear infinite;
}

.welcome__logo {
  margin-bottom: 20px;
}

.welcome__button {
  transition: transform 0.3s;
}

.welcome__button:hover {
  transform: scale(1.1);
}

@keyframes pulse {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: black;
}

p {
  margin: 0;
  padding: 0;
}

button,
input {
  outline: none;
  border: 1px solid transparent;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  background: #1d1f1f;
}

.container {
  width: 1140px;
  max-width: 100%;
  margin: 0 auto;
}

.button {
  border-radius: 27px;
  background-color: #ff3c00;
  color: #fff;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 700;
  padding: 15px 30px;
  cursor: pointer;
  transform: 0.4s;
}

.title {
  font-size: 36px;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
}

.title--black {
  color: black;
}

.bigtitle {
  font-size: 48px;
  color: #202123;
  font-weight: 700;
}

.bigtitle--white {
  color: #fff;
}