.welcome {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url(../../img/bg/bottom-pic.jpg) center no-repeat;
  background-size: cover;
  position: relative;

  &:before {
    content: "";
    display: block;
    background: url(../../img/bg/bottom-pic-ot.png) center no-repeat;
    background-size: cover;
    width: 1200px;
    height: 736px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0.2;
  }

  &__content {
    text-align: center;
    animation: pulse 0.8s linear infinite;
  }

  &__logo {
    margin-bottom: 20px;
  }

  &__button {
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }
}

@keyframes pulse {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
