.news {
  &__title {
    margin-bottom: 47px;
  }

  &__list{
      display: flex;
  }

  &__item {
    width: calc(100% / 2);
    margin-right: 30px;
    border-radius: 6px;
    overflow: hidden;
    position: relative;
    height: 390px;
    display: flex;
    align-items: flex-end;
    padding: 0 0 20px 20px;

    &-picture {
      height: 100%;
      width: 100%;
      object-fit: cover;
      position: absolute;
      left: 0;
      top: 0;
    }

    &-content {
      position: relative;
    }

    &-date {
      font-size: 14px;
      color: #ffffff;
      font-weight: 400;
      display: flex;
      align-items: center;
      margin-bottom: 10px;

      &-month {
        margin-right: 8px;
      }
    }

    &-title {
      font-size: 24px;
      color: #ffffff;
      font-weight: 700;
      margin-bottom: 5px;
    }

    &-text {
      line-height: 21px;
      color: #ffffff;
      font-weight: 400;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}
