.reviews {
  background-color: #fff;
  padding: 100px 0;

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: -20px;
  }

  &__item {
    width: 48%;
    margin-right: 30px;
    border-radius: 4px;
    background-color: #f4f5f5;
    overflow: hidden;
    padding: 48px 22px 35px;
    margin-bottom: 20px;

    &:nth-child(2) {
      margin-right: 0;
    }

    &:last-child {
      margin-right: 0;
    }

    &-bar {
      display: flex;
      align-items: center;
      margin-bottom: 21px;
    }

    &-avatar {
      width: 145px;
      height: 145px;
      border: 1px solid transparent;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 20px;

      &-icon {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }

    &-link {
      font-size: 24px;
      color: #2e77a4;
      font-weight: 700;
      text-decoration: underline;
    }

    &-row {
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-title {
      font-size: 30px;
      color: #1a1b1e;
      font-weight: 700;
      margin-bottom: 21px;
    }

    &-text {
      font-size: 16px;
      line-height: 27px;
      color: #818181;
      font-weight: 400;
      margin-bottom: 46px;
    }

    &-date {
      font-size: 14px;
      color: #1a1b1e;
      font-weight: 400;
    }
  }
}
