.services {
  background-color: #fff;
  padding-bottom: 89px;
  padding-top: 100px;

  &__item {
    display: flex;
    margin-bottom: 50px;

    &:last-child {
      margin-bottom: 0;
    }

    &-content{
        margin-top: 14px;
    }

    &-img {
      width: 458px;
      height: 330px;
      border-radius: 6px;
      margin-right: 57px;
      flex-shrink: 0;
      overflow: hidden;

      &-pic {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }

    &-title {
      font-size: 36px;
      color: #000000;
      font-weight: 700;
      margin-bottom: 30px;
    }

    &-text {
      line-height: 27px;
      color: #333333;
      font-weight: 400;
      margin-bottom: 35px;
    }
  }
}
