.features {
  margin-top: -156px;

  &__list {
    display: flex;
  }

  &__item {
    width: calc(100% / 3);
    margin-right: 30px;
    background-color: #fff;
    border-radius: 6px;
    padding: 30px 30px 50px;

    &:last-child {
      margin-right: 0;
    }

    &-icon {
      margin-bottom: 10px;
    }

    &-title {
      font-size: 30px;
      color: #000000;
      font-weight: 700;
      margin-bottom: 15px;
    }

    &-option {
      display: flex;
      align-items: center;
      margin-bottom: 10px;

      &-text {
        line-height: 21px;
        color: #666666;
        font-weight: 400;
        margin-right: 20px;

        &:last-child {
          margin-right: 0;
        }

        span {
          font-weight: 700;
          color: black;
        }
      }
    }

    &-text {
      font-size: 14px;
      line-height: 21px;
      color: #666666;
      font-weight: 400;
      margin-bottom: 20px;
    }

    &-button {
      .button {
        background: transparent;
        color: #ff5400;
        border-color: #ff5400;
        transition: 0.5s;

        &:hover {
          background: #ff5400;
          color: #fff;
        }
      }
    }
  }
}
