.form {
  background-color: #f4f5f5;
  padding: 78px 0;

  &__title {
    margin-bottom: 57px;
  }

  &__row {
    display: flex;
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__label {
    flex: 1;
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__input {
    border-radius: 4px;
    background-color: #ffffff;

    font-size: 18px;
    color: #666666;
    font-weight: 400;
    width: 100%;
    padding: 20px 31px;
  }

  &__textarea {
    outline: none;
    resize: none;
    height: 230px;
    border: 1px solid transparent;
  }

  &__subtitle {
    font-size: 18px;
    color: #1a1b1e;
    font-weight: 400;
    white-space: nowrap;
  }

  &__option-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: -12px;
    margin-right: -10px;
    margin-left: 10px;

    &-item {
      margin-right: 10px;
      margin-bottom: 12px;

      &:last-child {
        margin-right: 0;
      }
    }

    &-link {
      display: inline-block;
      border-radius: 17px;
      background-color: #e7e7e7;

      font-size: 13px;
      color: #303030;
      font-weight: 400;
      padding: 7px 18px;
      transition: 0.4s;

      &:hover {
        background: #f2d3c8;
      }
    }
  }

  &__button {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .button {
      &:first-child {
        background: #000;
      }

      &:last-child {
        margin-left: 10px;
      }
    }
  }
}
